import { Component } from 'base-component';
// #----------------------------------------# //
// #---- Component (cmp-frequent-forms) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {};

class FrequentForms extends Component {
	/*@ngInject*/
	controller($scope, $filter, ssAsync, ssFormTemplatesApi, $location, ssToastHlp) {

		var formTemplates;
		// view model for tracking state
		const vm = $scope.vm = { // eslint-disable-line
			formTemplates: [],
			loading: false
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line
			send(id) {
				$location.url(`/ec-forms/send/FormTemplate/${id}`);
			}
		};

		// KEEP this block once 'featureRecentlySent' feature flag is TRUE in PROD or removed

		vm.formSearchHeader = 'recently sent';

		$scope.$watch('vm.formSearchText', function(nv, ov) {
			if (nv === ov && !nv) { return; };
			vm.formTemplates = $filter('filter')(formTemplates, { '_name': nv });
		});

		(ssAsync(function*() {
			vm.loading = true;
			try {
				formTemplates = yield ssFormTemplatesApi.recentlySent({
					limit: 50
				});
				vm.formTemplates = [...formTemplates];
			} catch (err) {
				ssToastHlp.error(`There was an error retrieving recently sent form templates`, `Error`, false);
				console && console.error && console.error(err);
			} finally {
				vm.loading = false;
			};
		})());
	}
}

// #-- END Component (cmp-frequent-forms) --# //
// #----------------------------------------# //

angular.module('ec-dashboard-components').directive('cmpFrequentForms', () => new FrequentForms(props, 'frequent-forms'));
