const _hlp = {
	// #----------------------------------------# //
	// #---- Helper (optionalFieldGroupHlp) ----# //

	validate(fieldList, errorMessage) {
		const defaultMessage = 'One of the fields in this group is required.'
		var valid = false;
		for (var q = 0; q < fieldList.length; q++) {
			if (fieldList[q] != null) {
				valid = valid || fieldList[q].length > 0;
			}
			else {
				valid = valid || false;
			}
		}
		if (valid) { return valid; }
		if (errorMessage == null) { errorMessage = defaultMessage; }

		return errorMessage;
	}

	// #-- END Helper (optionalFieldGroupHlp) --# //
	// #----------------------------------------# //
};
/**
	* This helper is used to ensure that at least one of the fields passed in has a value. If it is false it will return an error message.
	*/
function optionalFieldGroupHlp() { return _hlp; }
angular.module('ec-dashboard-helpers').factory('optionalFieldGroupHlp', optionalFieldGroupHlp);
