import {Component} from 'base-component';
// #--------------------------------------# //
// #---- Component (cmp-summary-card) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	data: '=',
	centerNumber: '='
};

class SummaryCard extends Component {
	/*@ngInject*/
	controller($scope) {

		// view model for tracking state
		const vm = $scope.vm = { // jshint ignore:line
			graphConfig: {
				percentageInnerCutout: 75,
				//segmentStrokeColor: '#25384d',
				//segmentStrokeWidth: 1,
				customTooltips: function () {}
			},
			highlighted: null
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line

		};

	}
}

// #-- END Component (cmp-summary-card) --# //
// #--------------------------------------# //

angular.module('ec-dashboard-components').directive('cmpSummaryCard', () => new SummaryCard(props, 'summary-card') );
