import {Segment} from 'base-segment';
// #-----------------------------------------# //
// #---- Segment (/index/segment-header) ----# //

class Header extends Segment {
	/*@ngInject*/
	controller($injector, $scope, ssAsync, ssActiveUserSvc, $location, ssFormInstancesApi, ssNotificationsSvc, moment) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line

		// hookup segment logic
		const logic = $injector.get('segmentIndexHeaderLogic');// jshint ignore:line

		/////
		// view model for tracking state
		//
		const vm = $scope.vm = { // jshint ignore:line
			hideWelcome: false,
			greetingTime: logic.getGreetingTime(parseFloat(moment().format('HH'))),
			notificationStats: {},
			summaryCardData: [{
				label: 'Urgent Forms',
				value: 0,
				color: 'rgba(229, 88, 76, 1)',
				highlight: 'rgba(229, 88, 76, 1)'
				//class:'urgent'
			}, {
				label: 'Denied Forms',
				value: 0,
				color: 'rgba(204, 137, 137, 1)',
				highlight: 'rgba(204, 137, 137, 1)',
				//class:'denied',
			}, {
				label: 'Forms Not Started',
				value: 0,
				color: 'rgba(236, 150, 65, 1)',
				highlight: 'rgba(236, 150, 65, 1)',
				//class:'not-started',
			}, {
				label: 'Forms In Progress',
				value: 0,
				color: 'rgba(86, 194, 163, 1)',
				highlight: 'rgba(86, 194, 163, 1)',
				//class:'in-progress',
			}]
		};

		vm.hideWelcome = $scope.$root.isPermitted.activeUser.idmAdministrator || false;


		/////
		// behavior that needs to be bound to the view
		//
		const go = $scope.go = { // jshint ignore:line
			href(url) {
				$location.url(url);
			},

			// this was added to go because eventually we will likely call this
			// on a timer and provide a button on the interface to "update"
			updatePanels: ssAsync(function*() {
				vm.headerLoading = true;

				try {

					let params = {};

					if (getResolveMethod() !== null) {
						params['resolvemethod'] = getResolveMethod();
					};

					const summary = routeVm.summary = yield ssFormInstancesApi.queryDashboardSummary(params);

					// recently added count
					vm.empAddedCount = summary._recentlyAddedEmployees;

					// recently added count
					vm.inboxCount = summary._formInboxCount;

					// urgent forms
					vm.urgentFormsCount = summary._urgentFormsCount;

					vm.summaryCardData[0].value = summary._urgentFormsCount;
					vm.summaryCardData[1].value = summary._deniedFormsCount;
					vm.summaryCardData[2].value = summary._notStartedFormsCount;
					vm.summaryCardData[3].value = summary._inProgressFormsCount;
					vm.summaryCardCenterNumber = summary._incompleteFormsCount;

				} catch (e) {
					console && console.log && console.log('Error', e);
				} finally {
					vm.headerLoading = false;
				}

			}),
		};

		function getResolveMethod() {
			const resolveMethod = $location.search().resolvemethod;

			if (resolveMethod === 'ReadModel' || resolveMethod === 'Mongo') {
				return resolveMethod;
			} else {
				return null;
			}
		}

		ssNotificationsSvc.getStats().then(stats => vm.notificationStats = stats);
		ssActiveUserSvc.getEmployee().then(user => vm.user = user._firstName);
		go.updatePanels();
	}
}

// #-- END Segment (/index/segment-header) --# //
// #-----------------------------------------# //

angular.module('ec-dashboard-segments').directive('segmentIndexHeader', () => new Header('header'));
