/*@ngInject*/
function logic() {
	// logic api
	return {
		/**
		 * Determine the English time-of-day greeting by hour
		 * @param {Number} currentHour Hour value passed in from Moment
		 * @returns {String} One of 'Morning', 'Afternoon', or 'Evening'
		 */
		getGreetingTime(currentHour) {
			var greeting = '';

			var splitAfternoon = 12; //24hr time to split the afternoon
			var splitEvening = 16; //24hr time to split the evening

			if (currentHour >= splitAfternoon && currentHour <= splitEvening) {
				greeting = 'Afternoon';
			} else if (currentHour >= splitEvening) {
				greeting = 'Evening';
			} else {
				greeting = 'Morning';
			}

			return greeting;
		}
	};
}
angular.module('ec-dashboard-segment-logic').factory('segmentIndexHeaderLogic', logic);
