import {Component} from 'base-component';
// #------------------------------------------# //
// #---- Component (cmp-reminders-widget) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	text: '@'
};

const employeeMap = new Map();
var empId, urgentForms;

class RemindersWidget extends Component {
	/*@ngInject*/
	controller($scope, $log, ssAsync, ssActiveUserSvc, ssFormInstancesApi, ssEmployeeApi, ssEnum, ssArrayHlp, ssToastHlp, ssDateByOrgHlp, ssDueDateHlp,
		ssMultiInitiativeFeatureDetectorSvc) {

		const routeVm = $scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope);

		// view model for tracking state
		const vm = $scope.vm = { // eslint-disable-line
			loadingContact: false,
			loading: false,
			urgentForms: [],
			isDIPEnabled: ssMultiInitiativeFeatureDetectorSvc.isDataIntegrationsEnabled()
		};

		// behavior that needs to be bound to the view
		$scope.go = {

			toggleContact(form) {
				(ssAsync(function* () {
					var emp;
					try {
						vm.loadingContact = true;
						if (!employeeMap.has(form.currentStepParticipantId)) {
							emp = yield ssEmployeeApi.get({
								'id': form.currentStepParticipantId,
								'include': 'PhoneNumbers,Emails'
							});
							employeeMap.set(form.currentStepParticipantId, emp);
						} else {
							emp = employeeMap.get(form.currentStepParticipantId);
						}

						vm.empPhone = emp.bestContactNumber && emp.bestContactNumber.formatted || 'None';
						vm.empEmail = emp.bestContactEmail || 'None';
						vm.orgPersonId = emp._orgPersonId;

					} catch(e) {
						$log.error('ERROR', e);
					} finally {
						vm.loadingContact = false;
					}

				})());
			},

			sendReminder: ssAsync(function*(form) {

				form.sendingReminder = true;
				let participant = (form.currentStep._stepParticipant.name) ? `to ${form.currentStep._stepParticipant.name}` : ``;

				yield ssFormInstancesApi.postReminder({
					id: form.id
				}).then(() => {
					ssToastHlp.success(`to ${participant}`, 'Reminder Sent', false);
					form.sendingReminder = false;
				}).catch((err) => {
					ssToastHlp.error(`Sending a reminder to ${participant} did not succeed`, 'Error', false);
					$log.error('error sending reminder', err);
				});

				loadFormsAndProcess();
			})

		};

		function getCurrentStepIndex(form) {
			var stepIndex = form._steps.findIndex(function(step, index) {
				return step._formTemplateStepId === form._currentStepId;
			});
			return stepIndex + 1;
		}

	//	process form data - Set-up props for Urgent Forms and filter out non-urgent ones
		function processForms(forms) {

			if(!angular.isArray(forms)) {
				forms = [forms];
			}

			forms.forEach(function (form) {
				form.dueDateStatus = ssDueDateHlp.dueStatus(form, routeVm.org);
				form.isUser = form._employee.employeeId !== null && form._employee.employeeId === empId;

				//	Get the current step object
				if (form.currentStep == null) {
					form.currentStepMeta = `no step: no step action`;
					form.currentStepParticipantId = null;
				} else {

					if (form._lastReminderSentUtc) {
						form.reminderSentDate = ssDateByOrgHlp.utcIsoToRelativeMoment(form._lastReminderSentUtc, routeVm.org).format('M/D/YY');
					}

					form.currStepIndex = getCurrentStepIndex(form);
					form.currentStepActionName = form.currentStep._stepAction != null ? ssEnum.stepAction.byValue(form.currentStep._stepAction) : 'n/a';
					form.currentStepMeta = `step: ${form.currStepIndex}: ${form.currentStepActionName}`;
					form.currentStepParticipantId = form.currentStep._stepParticipant && form.currentStep._stepParticipant.employeeIds[0];
				}
			});

			ssArrayHlp.sortByObjectKey(forms, '_dueDateUtc');

			return forms;
		}

		function loadFormsAndProcess() {
			return ssAsync(function* () {
				try {
					urgentForms = yield ssFormInstancesApi.queryByOrg({
						include: 'steps',
						'filter[dueDateUtc]': ssDateByOrgHlp.safeNowRelativeTo(routeVm.org).add(4, 'days').toISOString(),
						'filter[status]': '0,2',
						limit: 5
					});
					vm.urgentForms = processForms(urgentForms);
				} catch(err) {
					ssToastHlp.error(`There was an error processing urgent forms`, 'Error', false);
					$log.error('There was an error processing urgent forms', err);
				}
			})();
		}

	//	Initialize the data
		ssAsync(function* () {
			vm.loading = true;
			try {
				empId = yield ssActiveUserSvc.getIdentityByType('employee');
				loadFormsAndProcess();
			} catch(err) {
				$log.error('An error occured initializing data', err);
			} finally {
				vm.loading = false;
			}
		})();

	}
}

// #-- END Component (cmp-reminders-widget) --# //
// #------------------------------------------# //

angular.module('ec-dashboard-components').directive('cmpRemindersWidget', () => new RemindersWidget(props, 'reminders-widget') );
