/*
This sleep helper is only meant to mitigate the eventual consistency on the UI
Any place where we notice data been not consistent add this sleep call between POST / PATCH and GET Call
*/
function sleepHlp($q, $timeout) {
	const DEFAULT_TIMEINMILLESECONDS = 1500;
	const _hlp = {
		sleep
	};


	function sleep(timeInMillseconds) {
		var deffered = $q.defer();
		$timeout(() => deffered.resolve(), timeInMillseconds || DEFAULT_TIMEINMILLESECONDS)
		return deffered.promise;
	}

	return _hlp;


}
angular.module('ec-dashboard-helpers').factory('sleepHlp', sleepHlp);
