import {Component} from 'base-component';
// #-----------------------------------------# //
// #---- Component (cmp-employee-widget) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	text: '@'
};

class EmployeeWidget extends Component {
	/*@ngInject*/
	controller($scope, $location, sleepHlp, ssAsync, ssEnum, ssEmployeeApi, ssValidationHlp, optionalFieldGroupHlp, ssToastHlp, ssFlagSvc, ssMultiInitiativeFeatureDetectorSvc) {

		var addEmployee;
		const ssnDashRegex = /^\d{3}-\d{2}-\d{4}$/;
		const ssnNoDashRegex = /^\d{9}$/;
		const emailType = ssEnum.emailType;
		const validator = $scope.validator = {
			ssn: ssValidationHlp.ssn(),
			email: ssValidationHlp.email(),
			required: ssValidationHlp.required(),
			externalId: ssValidationHlp.string({maxLength: 100})
		};

		// view model for tracking state
		const vm = $scope.vm = {
			canCreateEmployee: ssMultiInitiativeFeatureDetectorSvc.canCreateEmployee(),
			isGlobalDirectoryEnabled: ssMultiInitiativeFeatureDetectorSvc.isGlobalDirectoryEnabled(),
			addEmpMenuOpen: true,
			emp: {},
			errors: '',
			saving: false,
			empSearchResults: [],
			empSearchText: '',
			empSearchLoading: false,
			empSearchHeader: 'recently added',
		};

		const isGlobalDirectory = ssMultiInitiativeFeatureDetectorSvc.isGlobalDirectoryEnabled();

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			getHref(emp) {
				var ret = `/ec-employee/record/${emp.id}`;

				if (isGlobalDirectory) {
					// If we don't have an orgPersonId for some reason set the url to an empty string
					ret = emp._orgPersonId ? `/ec-directory/person-detail/${emp._orgPersonId}/personal` : '';
				}
				return ret;

			},
			closeEmpMenu() {
				vm.addEmpMenuOpen = false;
				vm.errors = '';
				vm.emp = {};
				$scope.$emit('ss-menu-close');
			},
			validateExternalId() {
				if (vm.emp._externalId == null) { return true; }

				return validator.externalId(vm.emp._externalId);
			},
			validateOr() {
				return optionalFieldGroupHlp.validate([vm.emp._sSN, vm.emp._externalId], 'Please enter a SSN or an External ID.');
			},
			addEmployee() {
				if (addEmployee == null) {

					if ((vm.emp._sSN != null && vm.emp._sSN === '') && (vm.emp._externalId != null && vm.emp._externalId === '')) {
						if (vm.emp._sSN != null && !ssnDashRegex.test(vm.emp._sSN) && !ssnNoDashRegex.test(vm.emp._sSN)) {
							vm.errors = 'SSN is not formatted correctly. ###-##-####';
							return;
						}
					}

					// final check for valid form
					if (!go.validateOr(vm.emp._sSN, vm.emp._externalId)) {
						vm.errors = 'Please enter a SSN or an External ID.';
						return;
					}

					vm.saving = true;
					if (ssnNoDashRegex.test(vm.emp._sSN)) {
						// add dashes
						vm.emp._sSN = vm.emp._sSN.substring(0, 3) + '-' + vm.emp._sSN.substring(3, 5) + '-' + vm.emp._sSN.substring(5);
					}
					addEmployee = ssAsync(function* () {
						try {
							let employee = yield ssEmployeeApi.add({ include: 'emails' }, vm.emp);
							if(vm.emp._workEmail) {
								employee._emails.push({ _emailAddress: vm.emp._workEmail,	_emailType: emailType.work, _isPrimary: false });
							}
							if(vm.emp._personalEmail) {
								employee._emails.push({ _emailAddress: vm.emp._personalEmail, _emailType: emailType.personal, _isPrimary: false });
							}
							yield employee._emails.$save();
							yield sleepHlp.sleep();
							if (employee.id != null) {
								let path = `/ec-employee/record/${employee.id}/personal-edit`;
								$location.path(path);
							} else {
								addEmployee = null;
								vm.saving = false;
								ssToastHlp.error('There was an error adding the employee. Please try again later.');
							}
						} catch (err) {
							addEmployee = null;
							vm.saving = false;
						}
					})();
				}
			}
		};

		$scope.$watch('vm.empSearchText', searchText => {
			(ssAsync(function* () {
				vm.empSearchLoading = true;
				const baseEmpReqParams = {
					fields: 'firstname,lastname,birthDateUtc,gender,maritalStatus,race,ethnicity,hiredate,ssn,createdUtc,orgPersonId',
					include: 'phoneNumbers,addresses,emails,positions,emergencyContacts,assignments,statusHistory'
				};
				try {
					if (searchText != null && searchText.length > 0) {
						vm.empSearchResults = yield ssEmployeeApi.query(
							Object.assign({}, baseEmpReqParams, {
								'filter[fullname]': searchText,
								limit: 50
							})
						);
						vm.empSearchHeader = 'search results';
					} else {
						vm.empSearchResults = yield ssEmployeeApi.query(
							Object.assign({}, baseEmpReqParams, {
								'sort[createdUtc]': 'DESC',
								limit: 50
							})
						);
						vm.empSearchHeader = 'recently added';
					}
				} catch (e) {
					console && console.error && console.error('ERROR', e);
				} finally {
					vm.empSearchLoading = false;
				}
			})());
		});

		$scope.$watch('vm.emp._sSN', (nv, ov) => {
			if (nv == null) { return; }
			var regex = /^(\d{3})\s?\-?\s?(\d{2})\s?\-?\s?(\d{4})$/;
			var formatter = '$1-$2-$3';
			if(nv.length > 11) { nv = nv.substring(0,11); }
			vm.emp._sSN = nv.replace(regex, formatter);
		});
	}
}

// #-- END Component (cmp-employee-widget) --# //
// #-----------------------------------------# //

angular.module('ec-dashboard-components').directive('cmpEmployeeWidget', () => new EmployeeWidget(props,
	'employee-widget'));
